.popup__form {
    display: flex;
    flex-direction: column;
    width: 430px;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    align-items: center;
}

@media (max-width: 560px) {
    .popup__form {
        width: 100%;
    }
}